.heading1 {
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: rgb(44, 54, 250);
  border: 1px solid white;
  text-shadow:  0 1.5px #000000;
}

.heading2 {
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: rgb(44, 54, 250);
  border: 1px solid white;
  text-shadow:  0 1.5px #000000;
}

.parentSuper {
  width: 100vh;
}

.parent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0;
  padding: 0;
}

.image {
  height: 5cm;
  width: 5cm;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.bg1 {
  width: 10cm;
  height: 10cm;
  background: linear-gradient(to bottom, transparent 50%, rgb(0, 0, 0) 50%),
    radial-gradient(circle closest-side, #e2fb00, rgb(248, 141, 70));
  border: 1px solid black;
  border-radius: 12px;

}

.bg2 {
  width: 10cm;
  height: 10cm;
  background: linear-gradient(to bottom, transparent 50%, black 50%),
    radial-gradient(circle closest-side, #000000 35%, rgb(39, 39, 43));
  border: 1px solid black;
  border-radius: 12px;
}

.toggle {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1cm;
}

.toggle input[type="checkbox"] {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 0;
}

.toggle label {
  position: relative;
  display: flex;
  align-items: center;
}

.toggle label:before {
  content: '';
  width: 90px;
  height: 42px;
  background: #ccc;
  position: relative;
  display: inline-block;
  border-radius: 46px;
  transition: 0.2s ease-in;
}

.toggle label:after {
  content: '';
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  left: 0;
  top: -5px;
  z-index: 2;
  background: #000000;
  box-shadow: 0 0 5px #0002;
  transition: 0.2s ease-in;
}

.toggle input[type="checkbox"]:hover+label:after {
  box-shadow: 0 2px 15px 0 #0002, 0 3px 8px 0 #0001;
}

.toggle input[type="checkbox"]:checked+label:before {
  background: #818181;
}

.toggle input[type="checkbox"]:checked+label:after {
  background: #000000;
  left: 40px;
}

@media only screen and (min-width:800px) and (max-width:1440px) {
  .image {
    height: 4.5cm;
    width: 4.5cm;
  }

  .toggle label:before {
    content: '';
    width: 80px;
    height: 35px;
  }

  .toggle label:after {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
  }
}

@media only screen and (min-width:400px) and (max-width:800px) {
  .image {
    height: 4cm;
    width: 4cm;
  }

  .toggle label:before {
    content: '';
    width: 70px;
    height: 30px;
  }

  .toggle label:after {
    content: '';
    position: absolute;
    width: 35px;
    height: 35px;
  }

  .bg1,
  .bg2 {
    height: 8cm;
    width: 8cm;
  }

}

@media only screen and (min-width:250px) and (max-width:400px) {
  .image {
    height: 3.5cm;
    width: 3.5cm;
  }

  .toggle label:before {
    content: '';
    width: 70px;
    height: 25px;
  }

  .toggle label:after {
    content: '';
    position: absolute;
    width: 30px;
    height: 30px;
  }

  .bg1,
  .bg2 {
    height: 6cm;
    width: 6cm;
  }

}

@media only screen and (min-width:160px) and (max-width: 250px) {
  .image {
    height: 3cm;
    width: 3cm;
  }

  .toggle label:before {
    content: '';
    width: 55px;
    height: 20px;
  }

  .toggle label:after {
    content: '';
    position: absolute;
    width: 25px;
    height: 28px;
  }

  .bg1,
  .bg2 {
    height: 4cm;
    width: 4cm;
  }
}

@media only screen and (max-width: 160px) {
  .parent {
    width: 90vh;
  }

  .image {
    height: 3cm;
    width: 3cm;

    margin: 0.5cm;
  }

  .toggle label:before {
    content: '';
    width: 50px;
    height: 18px;
  }

  .toggle label:after {
    content: '';
    position: absolute;
    width: 22px;
    height: 25px;
  }

  .bg1,
  .bg2 {
    background: white;
    border: none;
    box-shadow: none;
  }

}